body.lock {
  overflow: hidden;
}

.popup {
  width: 100%;
  height: 100%;
  opacity: 0;
  visibility: hidden;
  z-index: 10000;
  background-color: rgba(0, 0, 0, .8);
  transition: all .8s;
  position: fixed;
  top: 0;
  left: 0;
  overflow-x: hidden;
  overflow-y: auto;
}

.popup.open {
  opacity: 1;
  visibility: visible;
}

.popup.open .popup__content {
  opacity: 1;
  transform: perspective(600px)translate(0)rotateX(0deg);
}

.popup__area {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.popup__body {
  min-height: 100%;
  justify-content: center;
  align-items: center;
  padding: 30px 10px;
  display: flex;
}

.popup__content {
  color: #000;
  max-width: 1200px;
  opacity: 0;
  background-color: #fff;
  padding: 20px;
  transition: all .8s;
  position: relative;
  transform: perspective(600px)translate(0, 200%)rotateX(45deg);
}

.popup-border {
  border-radius: 15px;
}

.popup__close {
  color: #000;
  padding: 10px;
  font-size: 20px;
  text-decoration: none;
  position: absolute;
  top: 10px;
  right: 10px;
}

.popup__title {
  margin: 0 0 1em;
  font-size: 20px;
}

