.preloader {
  width: 120px;
  height: 120px;
  border: 16px solid #f3f3f3;
  border-top-color: tomato;
  border-radius: 50%;
  animation: spin .6s linear infinite;
  position: absolute;
  top: 350px;
  left: calc(50% - 60px);
  transform: translateX(-50%);
}

@keyframes spin {
  0% {
